.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 110px;
  margin-bottom: 30px;
}

.headerTitle {
  font-size: 23px;
  color: #1D1D1B;
}

.headerCart {
  display: flex;
  align-items: center;
  cursor: pointer;
  transition: all 0.2s ease;
}

.headerCart p {
  font-size: var(--fs-text);
  font-weight: var(--fw-700);
  margin-top: 5px;
  margin-left: 10px;
  margin-right: 10px;
  transition: 0.2s ease;
}

.headerCart span {
  background-color: var(--color-green);
  height: 21px;
  width: 21px;
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-white);
  font-size: 12px;
}

.cartWrapper {
  display: flex;
}

.cartWrapper:hover * {
  color: var(--color-green);
  fill: var(--color-green);
}


@media (max-width: 510px) {
  .headerCart span {
    margin-left: 7px;
  }
}
@media (max-width: 320px) {
  .header {
    margin-bottom: 20px;
  }
}